/* Basic styles for the Messenger home UI */
.messenger-page { display: flex; flex-direction: column; height: 100vh; }
.messenger-header { display:flex; align-items:center; padding:12px 16px; border-bottom:1px solid #eee; background:#fff; }
.header-left h2 { margin:0; font-size:18px }
.header-center { flex:1; padding:0 12px }
.search-input { width:100%; padding:8px 10px; border-radius:8px; border:1px solid #ddd }
.header-right .new-chat-btn { padding:8px 12px }
.chats-list { overflow:auto; padding:8px; background:#fafafa; flex:1 }
.chat-row { display:flex; padding:10px; align-items:center; border-radius:8px; margin-bottom:8px; background:#fff; cursor:pointer; box-shadow:0 1px 0 rgba(0,0,0,0.03) }
.chat-row:hover { transform:translateY(-1px) }
.avatar-wrap { margin-right:10px }
.avatar { width:52px; height:52px; border-radius:50%; object-fit:cover; position:relative }
.avatar.online { box-shadow:0 0 0 3px rgba(76,175,80,0.12); }
.chat-meta { flex:1; display:flex; flex-direction:column }
.meta-top { display:flex; justify-content:space-between; align-items:center }
.chat-name { font-weight:600 }
.chat-time { font-size:12px; color:#888 }
.meta-bottom { display:flex; justify-content:space-between; align-items:center; margin-top:6px }
.chat-preview { color:#666; font-size:14px; overflow:hidden; text-overflow:ellipsis; white-space:nowrap; max-width:60% }
.unread-badge { background:#ff4d4f; color:#fff; padding:4px 8px; border-radius:16px; font-size:12px }
.list-end { text-align:center; padding:12px; color:#888 }
