.bg-selector { display:flex; flex-direction:column; gap:10px; }
.bg-presets { display:flex; gap:8px; }
.bg-preset { width:64px; height:40px; background-size:cover; background-position:center; border-radius:6px; border:2px solid transparent; cursor:pointer; }
.bg-preset.active { outline:3px solid #ffd89b; }
.bg-actions { display:flex; gap:8px; align-items:center; }
.btn-upload { background:#667eea; color:white; padding:6px 10px; border-radius:6px; cursor:pointer; }
.btn-online { background:#ffd89b; color:#5a4a2b; padding:6px 10px; border-radius:6px; border:none; cursor:pointer; }
