.chat-page { display:flex; flex-direction:column; height:100vh; }
.chat-header { display:flex; align-items:center; padding:10px; border-bottom:1px solid #eee; background:#fff }
.chat-title { font-weight:600; margin-left:12px }
.chat-messages { flex:1; padding:12px; overflow:auto; background:#f7f7f7 }
.msg { max-width:70%; margin-bottom:8px; padding:8px 10px; border-radius:12px }
.msg.me { margin-left:auto; background:#dcf8c6 }
.msg.them { margin-right:auto; background:#fff }
.chat-input { display:flex; padding:8px; border-top:1px solid #eee; background:#fff }
.chat-input input { flex:1; padding:8px 10px; border-radius:8px; border:1px solid #ddd }
.chat-input button { margin-left:8px; padding:8px 12px }
.msg-media { margin-bottom:6px }
.msg-status { font-size:11px; color:#666; margin-top:4px }
.typing-indicator { padding:6px 12px; color:#666; font-size:13px }
